// colors
$accent-color: #156963;
$primary-text-color: #2c1500;
$secondary-text-color: #000000;
$footer-text-color: #4f4f4f;
$plantuki-house-color: #333333;
$buttons-text-color: #fafefe;
$slider-pagination-color: rgba(51, 51, 51, 0.5);
$border-color: rgb(224, 224, 224);
$slider-gradient: linear-gradient(
  to right,
  rgba(130, 133, 126, 1),
  rgba(174, 185, 192, 1)
);
$primary-background: #ffffff;
$secondary-background: rgb(243, 244, 246);
$backdrop-background: rgba(51, 51, 51, 0.5);

// dark theme
$dark-theme-background: #333333;
$dark-theme-text: #fafefe;

// fonts
$primary-font: 'Montserrat', sans-serif;
$button-font: 'Nunito', sans-serif;
$logo-font: 'Poppins', sans-serif;
$footer-font: 'Work Sans', sans-serif;
$subscribe-btn: 'Raleway', sans-serif;

// breakpoints

$desktop: 1200px;
$desktopMd: 1024px;
$tablet: 768px;
$tabletMd: 800px;
$mobile: 320px;
